import "./portfolio.css";
import "../../config/common.css"


const Typewriter = require("typewriter-effect/dist/core");
const language = navigator.language || navigator.userLanguage;
const pageNames = require("../../config/pageNames.json")

const italianText =
  `In questa pagina puoi dare un'occhiata ad alcuni progetti che ho svolto per l'universit&agrave;.
  Clicca su un progetto qui sotto per avere pi&ugrave informazioni!`

const englishText =
  `On this page you can take a look at some projects I have done for university.
  Click on a project below for more information!`

const italianGithub =
  `La maggior parte dei progetti a cui sto lavorando si trovano sul mio GitHub.
  Non tutte le repo sono tenute in ordine; ne vale comunque la pena di dare un'occiata.`

const englishGithub =
  `Most of the projects I am working on are available on my GitHub.
  Not every repo is tidied up, but I think it is still worth it to have a look.`

const typewriter = new Typewriter('#splash', {
  autoStart: false,
  cursor: "_",
  delay: 50,
});

const collapsibles = document.getElementsByClassName("collapsible");

Array.from(collapsibles).forEach(coll => {
  coll.onclick = function () {
    this.classList.toggle("active");
    const content = this.nextElementSibling;
    content.style.display = content.style.display !== "flex" ? "flex" : "none"
  }
})

window.onload = () => {
  const links = document.getElementsByClassName("diagonal")
  const pdfs = document.getElementsByClassName("work")
  Array.from(links).forEach(link => {
    link.innerText = pageNames[link.innerText][language === "it-IT"? "it" : "en"]
  })
  Array.from(pdfs).forEach(link => {
    link.innerText = pageNames[link.innerText][language === "it-IT"? "it" : "en"]
  })

  const maintext = document.getElementById("maintext");
  maintext.innerHTML = language === "it-IT" ? italianText : englishText;

  const gitHubText = document.getElementById("github");
  gitHubText.innerText = language === "it-IT" ? italianGithub : englishGithub;

  typewriter
    .typeString("Portfolio")
    .start()
}
