#library {
  position: fixed;
  bottom: 0px;
  right: 0px;
  padding-right: 4vw;
  padding-bottom: 12vw;
}

.box {
  font-size: 30pt;
  margin-bottom:4vh;
}
.diagonal {
  transform: rotate(-45deg);
  display: inline-block;
  width: 30vw;
}


/* Style the button that is used to open and close the collapsible content */
.collapsible {
  background-color: #444;
  color: whitesmoke;
  cursor: pointer;
  padding: 8px;
  width: 100%;
  text-align: left;
  font-size: 16pt;
  outline: none;
  border:none;
  margin-top: 2vh;
  border-left: 2px solid white;
  display: flex;
}

.active:before {
  content: ">"; /* Unicode character for "minus" sign (-) */
  height: 100%;
  animation: down .1s ease-out;
  animation-fill-mode: forwards;
  vertical-align: middle;
}

.collapsible:before {
  content: ">";
  font-size: 16pt;
  color: whitesmoke;
  float: left;
  margin-right: 2vw;
  vertical-align: middle;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .collapsible:hover {
  background-color: #222;
}

/* Style the collapsible content. Note: hidden by default */
.content {
  padding: 0 18px;
  overflow: hidden;
  background-color: #222;
  color: whitesmoke;
  border-left: 2px solid white;
  display: none;
  padding-bottom: 18px;
  flex-direction: row-reverse;
}

.content p:first-child {
  vertical-align: top;
  text-align: justify;
}

.content img {
  display: inline-block;
  width: 30%;
  margin:18px;
  object-fit: contain;
}

.textWithImage {
  display: inline-block;
}

@keyframes down {
  100% {
    transform:rotate(90deg);
  }
}

.item {
  padding: 0px;
}


